/* #Main Landing Section Home Page  */

#header {
  width: 100%;
  height: 100%;
  margin-top: 5rem;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 40px;
  font-weight: 700;
  color: purple;
}

#header .brand-name {
  color: blue;
}

#header h2 {
  color: #484848;
  font-size: 24px;
}

#header .btnA {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: blue;
  border: 2px solid blue;
  text-decoration: none;
}

 .btnA:hover {
  background: blue;
  color: #fff !important;
}

.header-img {
  text-align: right;
  
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}





